*, :before, :after {
  box-sizing: border-box;
}

.grid {
  flex-flow: wrap;
  margin: 0 -.5rem;
  display: flex;
}

.grid--nogutter {
  margin: 0;
}

.grid--nogutter > .col {
  padding: 0;
}

.col {
  flex: 1 0;
  min-width: 0;
  max-width: 100%;
  padding: 0 .5rem;
}

@media (width <= 30rem) {
  .col--sm {
    flex-basis: 100%;
    max-width: 100%;
  }
}

@media (width <= 64rem) {
  .col--md {
    flex-basis: 100%;
    max-width: 100%;
  }
}

@media (width <= 85rem) {
  .col--lg {
    flex-basis: 100%;
    max-width: 100%;
  }
}

.col--align-top {
  align-self: flex-start;
}

.col--align-bottom {
  align-self: flex-end;
}

.col--align-middle {
  align-self: center;
}

.col--top, .col--bottom, .col--middle {
  flex-direction: column;
  display: flex;
}

.col--top {
  justify-content: flex-start;
}

.col--bottom {
  justify-content: flex-end;
}

.col--middle {
  justify-content: center;
}

.grid--start {
  justify-content: flex-start;
}

.grid--center {
  justify-content: center;
}

.grid--end {
  justify-content: flex-end;
}

.grid--around {
  justify-content: space-around;
}

.grid--between {
  justify-content: space-between;
}

.grid--reverse {
  flex-direction: row-reverse;
}

.col--first {
  order: -1;
}

.col--last {
  order: 1;
}

.col--fixed {
  flex: none;
}

.col--grow-2 {
  flex-grow: 2;
}

.col--grow-3 {
  flex-grow: 3;
}

.col--grow-4 {
  flex-grow: 4;
}

.col--grow-5 {
  flex-grow: 5;
}

.col--grow-6 {
  flex-grow: 6;
}

.col--grow-7 {
  flex-grow: 7;
}

.col--grow-8 {
  flex-grow: 8;
}

.col--grow-9 {
  flex-grow: 9;
}

.col--grow-10 {
  flex-grow: 10;
}

.col--grow-11 {
  flex-grow: 11;
}

.col--1 {
  flex-basis: 8.33333%;
  max-width: 8.33333%;
}

.col--2 {
  flex-basis: 16.6667%;
  max-width: 16.6667%;
}

.col--3 {
